import React, { useCallback, useEffect, useState } from "react";
import { Images } from "shared/images";
import CustomDropdown from "components/atom/dropdown";
import SearchBar from "components/atom/searchBar";
import { useHistory } from "react-router-dom";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import "./style.scss";
import { TruncateString, toggleClasses } from "components/utility";
import CheckClickOutside from "components/atom/checkOutsideClick/checkOutsideClick";
import { useSelector } from "react-redux";
import { URL_HEAD } from "shared/api";
import { VERSION } from "shared/constants";

function Header({ profileData, sidebarToggle, SetSidebarToggle }) {
  const history = useHistory();
  const adminDetails = useSelector((state) => state.auth.adminDetails);
  const [showProfileDrop, setShowProfileDrop] = useState(false);
  const [showNotificationDrop, setShowNotificationDrop] = useState(false);
  const [image, setImage] = useState("");
  const [notificationData, setNotificationData] = useState([
    {
      title: "Pharmacy Users",
      Icon: "PharmacyUsers",
      details: "Lorem ipsum is dummy text, more info is needded",
      time: "10:30 AM",
    },
    {
      title: "Locum Users",
      Icon: "LocumUsers",
      details: "Lorem ipsum is dummy text, more info is needded",
    },
    {
      title: "Invoices Generated",
      Icon: "InvoicesGenerated",
      details: "Lorem ipsum is dummy text, more info is needded",
      time: "10:30 AM",
    },
    {
      title: "Vacancies",
      Icon: "Vacancies",
      details: "Lorem ipsum is dummy text, more info is needded",
      time: "10:30 AM",
    },
  ]);

  const toggleSidebar = useCallback(() => {
    toggleClasses("main", "slide-active", sidebarToggle ? "add" : "remove");
  }, [sidebarToggle]);

  useEffect(() => {
    toggleSidebar();
  }, [toggleSidebar]);

  useEffect(() => {
    setImage(adminDetails?.photo);
  }, [adminDetails]);

  useEffect(() => {
    toggleClasses("profile_info", "active", showProfileDrop ? "add" : "remove");
  }, [showProfileDrop]);

  useEffect(() => {
    toggleClasses(
      "noficaition_btn",
      "active",
      showNotificationDrop ? "add" : "remove"
    );
  }, [showNotificationDrop]);

  return (
    <>
      <header className="header">
        <div className="container-fluid px-3 px-md-4">
          <div className="d-flex flex-wrap align-items-center justify-content-between">
            <div className="d-flex align-items-center col_lft">
              <div
                className="logo_icon me-3"
                onClick={() => history.push(ROUTE_CONSTANTS.DASHBOARD)}
              >
                <img
                  src={Images.locumLogoIcon}
                  alt="Locumbay"
                  width={40}
                  className="w-100"
                />
              </div>
              <button
                className="hamburger me-2 me-lg-4"
                onClick={() => {
                  SetSidebarToggle((prev) => !prev);
                }}
              >
                <img src={Images.hamburger} alt="hamburger" width="20" />
              </button>
              <label> {VERSION ? "v" + VERSION : ""}</label>
              {/* <SearchBar placeholder={"Search here..."} searchAPI={searchAPI} /> */}
            </div>
            <div className="d-flex align-items-center col_rht">
              <CheckClickOutside onClick={() => setShowNotificationDrop(false)}>
                <div className="position-relative notification_drop">
                  <button
                    className="noficaition_btn"
                    onClick={() => setShowNotificationDrop((prev) => !prev)}
                  >
                    <img src={Images.notification} alt="" />
                    <span className="badge">
                      {notificationData?.length ? notificationData?.length : ""}
                    </span>
                  </button>
                  <CustomDropdown
                    setShow={setShowNotificationDrop}
                    show={showNotificationDrop}
                  >
                    <div className="d-flex  flex-wrap align-items-center justify-content-between dropdown-header">
                      <h6 className="h6 mb-0">Notifications</h6>
                      <button
                        className="btn"
                        onClick={() => setNotificationData([])}
                      >
                        Clear all
                      </button>
                    </div>

                    <ul className="list-unstyled dropdown-body">
                      {notificationData && notificationData?.length > 0
                        ? notificationData.map((i, idx) => {
                          return (
                            <li key={idx}>
                              <h6 className="h6">{i.details}</h6>
                              <span>{i.title} </span>
                            </li>
                          );
                        })
                        : null}
                    </ul>

                    <div className="dropdown-footer text-center">
                      <button
                        className="btn btn-sm btn-primary"
                        onClick={() => {
                          history.push(ROUTE_CONSTANTS.NOTIFICATION_LIST);
                          setShowNotificationDrop(false);
                        }}
                      >
                        View all
                      </button>
                    </div>
                  </CustomDropdown>
                </div>
              </CheckClickOutside>
              <div className="position-relative d-flex align-items-center ms-3 profile_info">
                <div className="avtar avtar50">
                  <img
                    src={image ? URL_HEAD + image : Images.defaultUserPic}
                    alt="Locumbay"
                    width={55}
                  />
                </div>

                <div className="author_info ms-2 d-none d-lg-block">
                  <h6 className="h6 text-capitalize">
                    {TruncateString(
                      adminDetails.first_name + " " + adminDetails.last_name,
                      12,
                      12
                    )}
                  </h6>
                  <small>Admin</small>
                </div>

                <CheckClickOutside onClick={() => setShowProfileDrop(false)}>
                  <button
                    className="p-0 btn-transparent arrow_down"
                    onClick={() => {
                      setShowProfileDrop((prev) => !prev);
                    }}
                  >
                    <img src={Images.arrowDown} alt="" width={20} />
                  </button>
                  <CustomDropdown
                    setShow={setShowProfileDrop}
                    show={showProfileDrop}
                  >
                    <div className="author_info ms-2 pt-3 px-3 d-lg-none text-center">
                      <h6 className="h6 text-capitalize">
                        {adminDetails.first_name + " " + adminDetails.last_name}
                      </h6>
                      <small>Admin</small>
                      <hr className="mb-0 d-lg-none" />
                    </div>
                    <div className="user-body">
                      {profileData && profileData?.length > 0
                        ? profileData.map((i, idx) => {
                          return (
                            <div
                              key={idx}
                              className="dropDown-list"
                              onClick={() => {
                                i.onClick();
                                setShowProfileDrop((prev) => !prev);
                              }}
                            >
                              <em>
                                <img
                                  src={i?.image}
                                  className="img-fluid"
                                  alt="icon"
                                  width={20}
                                />
                              </em>
                              <span>{i.title} </span>
                            </div>
                          );
                        })
                        : null}
                    </div>
                  </CustomDropdown>
                </CheckClickOutside>
              </div>
            </div>
          </div>
        </div>
      </header>
    </>
  );
}

export default Header;
